




















































import { Component,Vue } from "vue-property-decorator";

@Component
export default class SideBar extends Vue{
  value: string;
  context: string;
  hideHeader: boolean;

  constructor() {
    super();

    this.value = '';
    this.context = '';
    this.hideHeader = true;
  }

  async onContext(ctx: any) {
    this.context = ctx
    console.log()
  }




}
